<template>
    <div class="page">
        <MyHeader>
            <div>我的资产</div>
        </MyHeader>
        <div class="content circle-content">
            <div class="circle-out">
                <div class="circle-center">
                    <div class="circle-inner">
                        <div class="inner-top">￥0.00</div>
                        <div class="inner-bottom"><span>0.00</span></div>
                    </div>
                </div>
            </div>
            <div class="circle-item">
                <span class="yue">余额</span>
                <span class="lyuan">良元</span>
            </div>
        </div>
        <div class="content mb5">
            <ul class="e-list">
                <li class="bb-line">
                    <a class="arrow-right" href="javascript:void(0);">
                        <span class="item-text">钱包余额</span>
                        <span class="text-right">￥0.00</span>
                    </a>
                </li>
                <li >
                    <a class="arrow-right" href="javascript:void(0);">
                        <span class="item-text">支票剩余额度</span>
                        <span class="text-right">￥0.00</span>
                    </a>
                </li>
                <li>
                    <a class="arrow-right" href="javascript:void(0);">
                        <span class="item-text">良元余额</span>
                        <span class="text-right text-right-m">0.00</span>
                    </a>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import "lib-flexible"
import MyHeader from "./header";
export default {
    name:"myAssets",
    data:function(){
        return {
            title:"MyAssets"
        }
    },
    components:{MyHeader}
};
</script>

<style scoped>
.page{
    width:100%;
    height: auto;
    overflow: hidden;
}
.mb5{
    /* margin-bottom: 5rem; */
}
.content{
    height: auto;
    overflow: hidden;
    border-bottom: 1px solid #ddd;
}
.circle-content{
    padding: 45px 0;
}
.circle-out{
    margin: 0 auto;
    width: 500px;
    height: 500px;
    overflow: hidden;
    background:#f8f8f8;
    border-radius: 100%;
}
.circle-center{
    margin: 47.5px auto;
    width: 405px;
    height: 405px;
    overflow: hidden;
    background:#fedd2b;
    border-radius: 100%;
}
.circle-inner{
    margin: 22.5px auto;
    width: 360px;
    height: 360px;
    overflow: hidden;
    background:#ffffff;
    border-radius: 100%;
}
.inner-top,.inner-bottom{
    text-align: center;
    color: #000;
    width: 210px;
    margin: 0 auto;
}
.inner-top{
    font-size: 60px;
    font-weight: 700;
    font-style: normal;
    border-bottom: 1px solid #ddd;
    margin-top: 90px;
    padding-bottom: 20px;
    margin-bottom: 20px;
}
.inner-bottom{
    font-size: 32px;
}
.inner-bottom span{
    padding-left: 35px;
    padding-top: 5px;
    background: url(./images/icon_m_01.png) no-repeat left center;
    background-size: 30px;
}
.circle-item{
    text-align: center;
    padding-top: 40px;
    padding-bottom: 20px;
}
.circle-item span{
    position: relative;
    display: inline-block;
    padding-left: 45px;
    margin: 0 18px;
    color: #777;
    font-size: 26px;
}
.circle-item span::before{
    content:"·";
    position: absolute;
    width: 40px;
    height: 40px;
    top:-115px;
    left: 0;
    font-size: 180px;
}
.circle-item span.yue::before{
    color: #724c6c;
}
.circle-item span.lyuan::before{
    color: #dddddd;
}
.e-list{
    padding-left:30px; 
}
.e-list li{
    padding: 28px 0;
}
.e-list li.bb-line{
    border-bottom: 1px solid #ddd;
}
.e-list li a{
    display: block;
    height: auto;
    overflow: hidden;
    margin-right: 30px;
    padding-right: 48px;
}
.e-list li a.arrow-right{
    background: url(./images/icon_arrow_right.png) no-repeat center right;
    background-size: 22px;
}
.e-list li a:focus,
.e-list li a:hover {
  text-decoration: none;
  color: #000;
}
.e-list li a span{
    display: inline-block;
    line-height: 40px;
}
.e-list li a .item-text{
    float: left;
    font-size: 28px;
    color: #000;
}
.e-list li a .text-right{
    float: right;
    font-size: 26px;
    color: #82838a;
}
.e-list li a .text-right-m{
    padding-left: 35px;
    background: url(./images/icon_m_02.png) no-repeat left center;
    background-size: 28px;
}

</style>



